import sidebar from './sidebar';
import toolbar from './toolbar';
import onAction from './handlers/onAction';
import createRenderToolbar from './createRenderToolbar';

export default function createTranslationPlugin(configs) {
  const config = configs.translate || {};
  return {
    sidebar: sidebar(config),
    toolbar: toolbar(config),
    renderToolbar:createRenderToolbar(config),
    onAction,
    models: [],
  };
}
